// This map is looped - adding key/value pairs will generate more classes
// Used by bg and text helpers
// I.E. .c-bg-danger-2
$color: () !default;
$color: map-merge(
  (
    'danger': (
      0: $color-danger-0,
      1: $color-danger-1,
      2: $color-danger-2,
      3: $color-danger-3,
      4: $color-danger-4,
      5: $color-danger-5,
      6: $color-danger-6,
      7: $color-danger-7,
      8: $color-danger-8,
      9: $color-danger-9
    ),
    'gray': (
      0: $color-gray-0,
      1: $color-gray-1,
      2: $color-gray-2,
      3: $color-gray-3,
      4: $color-gray-4,
      5: $color-gray-5,
      6: $color-gray-6,
      7: $color-gray-7,
      8: $color-gray-8,
      9: $color-gray-9
    ),
    'primary': (
      0: $color-primary-0,
      1: $color-primary-1,
      2: $color-primary-2,
      3: $color-primary-3,
      4: $color-primary-4,
      5: $color-primary-5,
      6: $color-primary-6,
      7: $color-primary-7,
      8: $color-primary-8,
      9: $color-primary-9
    ),
    'success': (
      0: $color-success-0,
      1: $color-success-1,
      2: $color-success-2,
      3: $color-success-3,
      4: $color-success-4,
      5: $color-success-5,
      6: $color-success-6,
      7: $color-success-7,
